.App {
  width: 80%;
  height: 100vh;
  margin: auto;
}

.flex-container {
  width: 100%;
  margin: auto;
  padding: 1px;
  align-items: center;
  justify-content: center;
  min-width: 500px;
  max-width: 1800px;
}

h4 {
  width: 100%;
  text-align: center;
}

.video {
  width: 80%;
  height: 200px;
  margin: 20px auto;
  padding: 20px 5px;
  background-color: #ecf0f1;
  box-shadow: 5px 10px #bdc3c7;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  max-height: 300px;
  max-width: 600px;
  position: relative;
  margin: 0 auto;
  top: 25%;
}

.modal-background {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 100;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal div {
  height: 100%;
}
.modal {
  width: 50%;
  height: 30%;
  margin: auto;
  z-index: 200;
  border: 1px solid blue;
  background-color: rgba(255, 255, 255, 1);
}

.flex-container div img {
  max-width: 100%;
  max-height: 80%;
  margin: auto;
}

@media screen and (min-width: 692px) {
  .flex-container {
    display: flex;
    flex-wrap: wrap;
  }

  .flex-container {
    width: 100%;
    margin: auto;
    padding: 1px;
    align-items: center;
    justify-content: center;
    min-width: 400px;
  }

  .video {
    width: 45%;
    height: 300px;
    margin: 20px auto;
    padding: 20px 5px;
    background-color: #ecf0f1;
    box-shadow: 5px 10px #bdc3c7;
  }

  .video:hover {
    cursor: pointer;
  }

  .modal-background {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .modal div {
    height: 100%;
  }
  .modal {
    width: 60%;
    height: 50%;
    z-index: 200;
    border: 1px solid blue;
    background-color: rgba(255, 255, 255, 1);
  }
}
